@media screen and (max-width: 968px) {
    .contactForm {
        max-width: 300px !important;
        padding: 0px 20px 20px 20px;
    }
}

.contactForm textarea {
    padding: 10px 15px 15px 15px;
}

/* css for checkbox in form */
.contactForm input[type="checkbox"] {
    height: 18px;
    width: 18px;
    margin: 22px 10px 0 5%;
    visibility: hidden;
}

.contactForm label {
    font-size: calc(1rem + 0.8vw);
    width: 100%;
}

.noSelect i {
    padding: 5px;
    font-size: calc(1rem + 0.4vw);
    border-radius: 10px;
    filter: contrast(0.5);
    filter: brightness(0.5);
    transition: 0.1s;
    margin: 0 10px 0 10px;
}

.contactForm label:hover {
    cursor: pointer;
}

.contactForm label:hover i {
    filter: contrast(0.9);
    filter: brightness(0.9);
    transition: 0.1s;
}

/* onclick css */
.contactForm input[type="checkbox"]:checked~i {
    filter: contrast(1);
    filter: brightness(1.01);
}

.contactForm kbd {
    background-color: white;
    color: rgb(184, 184, 184);
}

.contactForm input[type="checkbox"]:checked~kbd {
    font-weight: 800;
    color: black;
}

.mm i {
    color: var(--white);
    margin-top: 10px;
}

.emoji span {
    filter: brightness(0.5);
    cursor: pointer;
    color: rgb(219, 219, 219);
    transition: 0.4s;
}

.emoji span:hover {
    filter: brightness(0.7);
    transition: 0.4s;
}

.a {
    filter: brightness(1) !important;
}

.b {
    filter: brightness(1) !important;
}

.c {
    filter: brightness(1) !important;
}

.d {
    filter: brightness(1) !important;
}

.e {
    filter: brightness(1) !important;
}


