.contactForm select {
    color: var(--accentdark);
    background: var(--accentdim4);
    border: 2px solid var(--accentdim3);
    padding: 0 15px 0 15px;
    border-radius: 7px;
    width: 100%;
    height: 50px;
    margin-bottom: 20px;

    font-size: 20px;
    font-weight: 600;
    transition: 0.15s;
}

.contactForm select:focus {
    outline: none;
    border: 2px solid var(--accent);
    background: var(--accentdim4);
    transition: 0.15s;
}

.contactForm select::placeholder {
    color: var(--accentdim0);
    font-weight: 400;
    font-size: 17px;
    transition: 0.15s;
}

